<template>
    <div class="box-changepassword">
        <div class="changepassword-title">Thay i mt khu</div>
        <div class="box-input-changepassword">
            <div class="name-changepassword">Mt khu hin ti</div>
            <input ref="current" id="currentpassword" class="input-changepassword" type="password" placeholder="Nhp mt khu hin ti" v-model="currentpassword">
            <div class="icon-eye-changepasswword" @click="clickBtnIconEyeCurrentPassword()" :class="{'hide-currentpassword': isShowCurrentPassword}"></div>
        </div>
        <div class="box-input-changepassword">
            <div class="name-changepassword">Mt khu mi</div>
            <input id="newpassword" class="input-changepassword" type="password" placeholder="Nhp mt khu mi" v-model="newpassword">
                <div class="icon-eye-changepasswword" @click="clickBtnIconEyeNewPassword()" :class="{'hide-newpassword': isShowNewPassword}"></div>
        </div>
        <div class="box-input-changepassword">
            <div class="name-changepassword">Nhp li mt khu mi</div>
            <input id="retypenewpassword" class="input-changepassword" type="password" placeholder="Nhp li mt khu mi" v-model="retypenewpassword">
            <div class="icon-eye-changepasswword" @click="clickBtnIconEyeRetypeNewPassword()" :class="{'hide-retype-newpassword': isShowRetypeNewPassword}"></div>
        </div>
        <div class="box-btn-changepassword">
            <div class="btn-changepassword btn-save" @click="clickBtnSavePassword()">Lu</div>
        </div>
    </div>
</template>

<script>
import axios from 'axios';
import { mapGetters } from 'vuex';
import { eventBus } from'../main.js'
export default {
    name: 'DialogChangePassword',
    data() {
        return {
            isShowCurrentPassword: false,
            isShowNewPassword: false,
            isShowRetypeNewPassword: false,
            isShowDialogChangPassword: false,
            currentpassword: null,
            newpassword: null,
            retypenewpassword: null,
        }
    },
    computed: {
        ...mapGetters(['student']),
    },

    mounted() {
        this.$refs.current.focus();
    },
    
    methods: {
        /**
         * click vo btn lu mt khu
         * CreatedBy: TTThiep(16/01/2022)
         */
        clickBtnSavePassword(){
            var m = this;
            if(m.currentpassword == null || m.newpassword == null || m.retypenewpassword == null)
            {
                eventBus.$emit("errorWas", "Cc trng d liu khng c  trng");
                eventBus.$emit("isShowFormWaningWas", true);
            }
            else
            {
                if(m.currentpassword != m.student.PassWord)
                {
                    eventBus.$emit("errorWas", "Mt khu hin ti cha ng.");
                    eventBus.$emit("isShowFormWaningWas", true);
                }
                else
                {
                    if(m.newpassword == m.currentpassword)
                    {
                        eventBus.$emit("errorWas", "Mt khu mi khng c ging vi mt khu hin ti.");
                        eventBus.$emit("isShowFormWaningWas", true);
                    }
                    else
                    {
                        if(m.retypenewpassword != m.newpassword)
                        {
                            eventBus.$emit("errorWas", "Mt khu nhp li khng ging vi mt khu  nhp trc .");
                            eventBus.$emit("isShowFormWaningWas", true);
                        }
                        else
                        {
                            /**
                             * gi APi thay i mt khu
                             * CreatedBy: TTThiep(26/01/2022)
                             */
                            axios
                            .put(`https://www.vnedu.somee.com/api/v1/Students/UpdatePassword?StudentId=${m.student.StudentId}&PassWord=${m.newpassword}`)
                            .then(function (response){
                                if(response && response.data)
                                {
                                    eventBus.$emit("isShowToastMessageWas", true);
                                    eventBus.$emit("TitleToastMessageWas", "i mt khu thnh cng");
                                }
                            })
                            .catch(function (res){
                                console.log(res);
                            });
                            setTimeout(function(){
                                // chuyn n trang main
                                m.$router.push({ path: "/login" });
                            }, 1000);
                        }
                    }
                }
            }
        },

        /**
         * click vo btn con mt mt khu hin ti
         * CreatedBy: TTThiep(11/01/2022)
         */
        clickBtnIconEyeCurrentPassword(){
            this.isShowCurrentPassword = !this.isShowCurrentPassword;
            let passwordField = document.querySelector("#currentpassword");
            if(this.isShowCurrentPassword) 
            {
                passwordField.setAttribute("type", "text");
            } 
            else
            {
                passwordField.setAttribute("type", "password");
            }
        },

        /**
         * click vo btn con mt mt khu mi
         * CreatedBy: TTThiep(11/01/2022)
         */
        clickBtnIconEyeNewPassword(){
            this.isShowNewPassword = !this.isShowNewPassword;
            let passwordField = document.querySelector("#newpassword");
            if(this.isShowNewPassword) 
            {
                passwordField.setAttribute("type", "text");
            } 
            else
            {
                passwordField.setAttribute("type", "password");
            }
        },

        /**
         * click vo btn con mt nhp li mt khu mi
         * CreatedBy: TTThiep(11/01/2022)
         */
        clickBtnIconEyeRetypeNewPassword(){
            this.isShowRetypeNewPassword = !this.isShowRetypeNewPassword;
            let passwordField = document.querySelector("#retypenewpassword");
            if(this.isShowRetypeNewPassword) 
            {
                passwordField.setAttribute("type", "text");
            } 
            else
            {
                passwordField.setAttribute("type", "password");
            }
        },

        /**
         * click vo btn hy
         * CreatedBy: TTThiep(11/01/2022)
         */
        clickBtnCancel(){
            this.isShowDialogChangPassword = !this.isShowDialogChangPassword;
        },

        /**
         * click vo btn du X
         * CreatedBy: TTThiep(11/01/2022)
         */
        clickBtnX(){
            this.isShowDialogChangPassword = !this.isShowDialogChangPassword;
        }

    },

    created() {
        var m = this;

        // ly d liu t component TheHeader l true
        eventBus.$on('isShowDialogChangePasswordWas', (isShowDialogChangPasswordData) => {
            m.isShowDialogChangPassword = isShowDialogChangPasswordData;
        });

        // nhn d liu t component TheLogin
        eventBus.$on('userWas', (userData) =>{
            m.user = userData;
        })
    },
}
</script>