<template>
    <div class="t-login">
        <div class="login-opacity">
            <div class="box-login">
                <div class="login-top">
                    <div class="logo-login">
                    </div>
                </div>
                <input ref="username" type="text" class="box-input" placeholder="Tn ng nhp" v-model="phoneNumber">
                <div class="box-pass-user">
                    <input type="password" id="password" class="box-input" placeholder="Mt khu" v-model="passWord">
                    <div class="icon-eye" @click="clickBtnIconEye()" :class="{'hide-password': isHidePassword}"></div>
                </div>
                <div class="box-forgot-password">
                    <a class="link-forgot-password" @click="clickLinkForgotPassword()" >Qun mt khu?</a>
                </div>
                <button class="btn-login" @click="clickBtnLogin()" v-on:keyup.enter="keyupBtnLogin()">ng nhp</button>
            </div>
            <div class="title-copyright">Copyright  2021 - 2022 VNEDU</div>
        </div>
        <form-waning></form-waning>
        <toast-message></toast-message>
    </div>
</template>

<script>
import axios from "axios";
import FormWaning from '../views/FormWaning.vue'
import ToastMessage from '../views/ToastMessage.vue'
import { eventBus } from '../main.js';
import { mapGetters, mapMutations } from 'vuex';
export default {
    name: 'TheLogin',
    components:{
        FormWaning, ToastMessage
    },
    data() {
        return {
            isHidePassword: false,
            phoneNumber: null,
            passWord: null,
        }
    },
    computed:{
        ...mapGetters(['student']),
    },
    methods: {
        ...mapMutations(['setStudent']),
        /**
         * click vo btn con mt
         * CreatedBy: TTThiep(11/01/2022)
         */
        clickBtnIconEye(){
            this.isHidePassword = !this.isHidePassword;
            let passwordField = document.querySelector("#password");
            if(this.isHidePassword) 
            {
                passwordField.setAttribute("type", "text");
            } 
            else
            {
                passwordField.setAttribute("type", "password");
            }
        },

        /**
         * click vo link qun mt khu
         * CreatedBy: TTThiep(19/01/2022)
         */
        clickLinkForgotPassword(){
            // gi d liu n component FormWaning l 'true'
            eventBus.$emit("isShowFormWaningWas", true);
            // gi d liu n component FormWaning l 'C li xy ra, lin h vi nh trng  c tr gip.'
            eventBus.$emit("errorWas", "C li xy ra, lin h vi nh trng  c tr gip.");
        },

        /**
         * click vo btn login
         * CreatedBy: TTThiep(13/01/2022)
         */
        clickBtnLogin(){
            var m = this;
            if(m.phoneNumber == null || m.passWord == null)
            {
                // gi d liu n component FormWaning l 'true'
                eventBus.$emit("isShowFormWaningWas", true);
                // gi d liu n component FormWaning l 'Tn ng nhp hoc mt khu khng c  trng'
                eventBus.$emit("errorWas", "Tn ng nhp hoc mt khu khng c  trng");
            }
            else
            {
                axios
                .get(`https://www.vnedu.somee.com/api/v1/Students/getStudentByPhoneNumberAndPassword?PhoneNumber=${m.phoneNumber}&PassWord=${m.passWord}`)
                .then(function (response){
                    if(response && response.data)
                    { 
                        m.setStudent(response.data);
                        // chuyn n trang main
                        m.$router.push({ path: "/main" });
                    }
                    else{
                        // gi d liu n component FormWaning l 'true'
                        eventBus.$emit("isShowFormWaningWas", true);
                        // gi d liu n component FormWaning l 'Tn ti khon hoc mt khu khng chnh xc'
                        eventBus.$emit("errorWas", "Tn ti khon hoc mt khu khng chnh xc");
                    }
                })
                .catch(function (res){
                    console.log(res);
                });
                
            }
        },

           /**
         * click vo btn login
         * CreatedBy: TTThiep(13/01/2022)
         */
        keyupBtnLogin(){
            var m = this;
            if(m.phoneNumber == null || m.passWord == null)
            {
                // gi d liu n component FormWaning l 'true'
                eventBus.$emit("isShowFormWaningWas", true);
                // gi d liu n component FormWaning l 'Tn ng nhp hoc mt khu khng c  trng'
                eventBus.$emit("errorWas", "Tn ng nhp hoc mt khu khng c  trng");
            }
            else
            {
                axios
                .get(`https://www.vnedu.somee.com/api/v1/Students/getStudentByPhoneNumberAndPassword?PhoneNumber=${m.phoneNumber}&PassWord=${m.passWord}`)
                .then(function (response){
                    if(response && response.data)
                    { 
                        m.setStudent(response.data);
                        // chuyn n trang main
                        m.$router.push({ path: "/main" });
                    }
                    else{
                        // gi d liu n component FormWaning l 'true'
                        eventBus.$emit("isShowFormWaningWas", true);
                        // gi d liu n component FormWaning l 'Tn ti khon hoc mt khu khng chnh xc'
                        eventBus.$emit("errorWas", "Tn ti khon hoc mt khu khng chnh xc");
                    }
                })
                .catch(function (res){
                    console.log(res);
                });
                
            } 
        },
    },
    mounted() {
        // focus vo  nhp tn ng nhp
        this.$refs.username.focus();
    },
}
</script>